import React from 'react';
import logo from './logo.svg';
import './App.css';
import {BrowserRouter as Router, Route, Link} from "react-router-dom";
import Home from './Pages/Home'
import Premium from './Pages/Premium'
import DevSchool from './Pages/DevSchool'
import Registration from './Pages/Registration'
import Blog from './Pages/Blog'

function App() {
  return (

<Router>

    <div>
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark fixed-top">
        <Link className="navbar-brand" to="/">Sketchride</Link>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
          <div className="navbar-nav">
            <Link className="nav-item nav-link active" to="/">Home <span className="sr-only">(current)</span></Link>
            <Link className="nav-item nav-link" to="/premium">Premium</Link>
            <Link className="nav-item nav-link" to="/devschool">ArsiSchool</Link>
            <Link className="nav-item nav-link" to="/registration">Registration</Link>
            <Link className="nav-item nav-link" to="/blog">Blog</Link>
          </div>
        </div>
    </nav>
      <Route path="/" exact component={Home}></Route>
      <Route path="/premium" component={Premium}></Route>
      <Route path="/devschool" component={DevSchool}></Route>
      <Route path="/registration" component={Registration}></Route>
      <Route path="/blog" component={Blog}></Route>
    </div>
    </Router>
  );
}

export default App;
