import React from 'react'
import Card from '../components/Card'
import Intro from '../components/Intro'

const intro ={
    content:`Lorem Ipsum is simply dummy text of the 
    printing and typesetting industry. Lorem Ipsum 
    has been the industry's standard dummy text ever 
    since the 1500s, when an unknown printer took a 
    galley of type and scrambled it to make a type 
    specimen book. It has survived not only five 
    centuries, but also the leap into electronic 
    typesetting, remaining essentially unchanged.
    Lorem Ipsum is simply dummy text of the 
    printing and typesetting industry. Lorem Ipsum 
    has been the industry's standard dummy text ever 
    since the 1500s, when an unknown printer took a 
    galley of type and scrambled it to make a type 
    specimen book. It has survived not only five 
    centuries, but also the leap into electronic 
    typesetting, remaining essentially unchanged.`,
}

const features =[
    {title:'Materi Belajar', desc:'Materi belajar menggambar yang ashooy banget cuy'
    },
    {title:'Waktu Belajar', desc:'Waktu belajar menggambar yang fleksibel banget cuy'
    },
    {title:'Metode Belajar', desc:'Materi belajar menggambar yang mengasyikan banget cuy'
    },
]

const cards =[
    {title:'Menjadi urbansketcher', imageUrl:'http://sketchrideproject.com/wp-content/uploads/2020/01/3-2-1024x680.jpg'
    },
    {title:'Belajar watercolor', imageUrl:'http://sketchrideproject.com/wp-content/uploads/2020/01/5-1024x768.jpg'
    },
]

const Premium = () => (
    <div className="container">
        <h1 style={{textAlign:'center'}}>Premium ini skoy</h1>
        <div className="row" style={{marginTop:40,marginBottom:100}}>
            <Intro content={intro.content}/>
            <div className="col-md-6" style={styles.box450border}>
                {
                    features.map(
                        feature=>(
                            <div>
                                <h5><strong>{feature.title}</strong></h5>
                                <p>{feature.desc}</p>
                            </div>
                        )
                    )
                }
            </div>
        </div>
        <div className="row d-flex justify-content-center" style={{marginBottom:50}}>
        {
            cards.map(
                card=>
                <Card
                    imageUrl={card.imageUrl}
                    title={card.title}
                />
            )
        }
        </div>
    </div>
)

const styles ={
    box450border:{
        height:'450px',
        border:'2px solid #f1f1f1',
        borderRadius:10,
        padding:30
    },
}

export default Premium