import React from 'react'


const form =(props)=>(
    <form onSubmit={props.onSubmitForm}>
                  <div className="form-group">
                    <label>First Name</label>
                    <input
                      type="text"
                      className="form-control" 
                      name="first_name"
                      value={props.first_name}
                      onChange={props.onChange}
                      placeholder="First Name"/>

                      <br/>
                    <label>Last Name</label>
                    <input
                      type="text"
                      className="form-control" 
                      name="last_name"
                      value={props.last_name}
                      onChange={props.onChange}
                      placeholder="Last Name"/>

                      <br/>
                    <label>Email</label>
                    <input
                      type="email"
                      className="form-control" 
                      name="email"
                      value={props.email}
                      onChange={props.onChange}
                      placeholder="Email"/>
                  </div>
                <button className='btn btn-success' type="submit" disabled={props.buttonDisabled}>Submit</button>                  
              </form>
)

export default form