import React,{Component} from 'react';
import axios from 'axios'
import Members from '../components/Members/Members'
import Form from '../components/Form/Form'

class Registration extends Component{

constructor(props){
  super(props)
  this.state ={
    members:[],
    first_name:'',
    last_name:'',
    email:'',
    buttonDisabled:false,
    memberIdSelected:null,
    formStatus:'create',
    saveStatus:'',
    first_name_saved:'',
    last_name_saved:'',
    email_saved:''
  }
}

componentDidMount(){
  axios.get('https://reqres.in/api/users')
  .then(response =>{
    console.log('response cuy', response)
    this.setState({members:response.data.data})
  })
  .catch(error =>{
    console.log('error cuy', error)
  })
}


inputOnchangeHandler = (event) =>{
  this.setState({[event.target.name]:event.target.value})
}

onSubmitHandler= (e) =>{
  console.log('form tersubmit kayaknya')
  e.preventDefault()

this.setState({buttonDisabled:true})

  var payload={
    first_name:this.state.first_name,
    last_name:this.state.last_name,
    email:this.state.email
  }

  var url = 'https://reqres.in/api/users'

if (this.state.formStatus === 'create'){
  this.addMember(url,payload)
}else{
  url = `https://reqres.in/api/users/${this.state.memberIdSelected}`
  this.editMember(url,payload)
}
}


editButtonHandler = (member) =>{
  this.setState({
    first_name : member.first_name,
    last_name: member.last_name,
    email:member.email,
    formStatus: 'edit',
    memberIdSelected:member.id
  })
}

addMember = (url,payload)=>{
  axios.post(url,payload)
  .then(response => {
    var members = [...this.state.members]
    members.push(response.data)
    this.setState({members, buttonDisabled:false, first_name:'', last_name:'',email:'', saveStatus:'Registered', 
    first_name_saved:payload.first_name,
    last_name_saved:payload.last_name,
    email_saved:payload.email
})
    console.log('form tersubmit beneran')
  })
  .catch(error => {
    console.log(error)
    this.setState({saveStatus:'Error when submit data'})
  })
}

editMember = (url,payload)=>{
  axios.put(url,payload)
      .then(response => {
        var members = [...this.state.members]
        var indexMember = members.findIndex(member => member.id === this.state.memberIdSelected)

    members[indexMember].first_name = response.data.first_name
    members[indexMember].last_name = response.data.last_name
    members[indexMember].email = response.data.email
    this.setState({
      members,
        first_name:'',
        last_name:'',
        email:'',
        buttonDisabled:false,
        formStatus:'create'

    })
  })
  .catch(error => {
    console.log(error)
  })
}

deleteButtonHandler = (id) => {
  var url = `https://reqres.in/api/users/${id}`
  axios.delete(url)
      .then(response => {
        if(response.status === 204){
          var members = [...this.state.members]
          var index = members.findIndex(member => member.id === id)
          members.splice(index,1)
          this.setState({members})
        }
  })
  .catch(error => {
    console.log(error)
  })
}


  render(){
  return (
    <div className="container">
    <br/>
    <br/>
    <br/>
      <h1>Registration</h1>
      <div className="row">
          
          <div className="col-md-6" style={{border:'1px solid black'}}>
          <br/>
            <h4>Fill the field below</h4>


              <Form 
                onSubmitForm={this.onSubmitHandler}
                onChange={this.inputOnchangeHandler}
                first_name={this.state.first_name}
                last_name={this.state.last_name}
                email={this.state.email}
                buttonDisabled={this.state.buttonDisabled}
              />


          <br/>
          </div>

          <div className="col-md-6" style={{border:'1px solid black'}}>
          <br/>
            <h2>Confirmation</h2>
            <div className="row">
                <div className="col-md-12">
                    <label>First Name : {this.state.first_name_saved}</label>
                      <br/>
                    <label>Last Name : {this.state.last_name_saved}</label>
                      <br/>
                    <label>Email : {this.state.email_saved}</label>
                  <h3>{this.state.saveStatus}</h3>                    
                  </div>
            </div>

          </div>
      </div>
    </div>
  );
}
}
export default Registration;
