import React from 'react'

const Card = (props) => (
    <div className="col-md-4">
        <div className="card text-center">
            <img src={props.imageUrl} className="card-img-top" alt="" style={{width:'350px', height:'200px'}}/>
            <div className="card-body">
                <h5 className="card-title">{props.title}</h5>
                <p>{props.subtitle}</p>
                <a href="http://wahangga.com/index.php/2017/02/04/travelsketch-di-kota-tua-jakarta/" target="blank" className="btn btn-primary">Mulai Belajar</a>
            </div>
        </div>
        <br/>
    </div>
)

export default Card