import React,{Component} from 'react'
import CardBlog from '../components/CardBlog'
import axios from 'axios'

class Blog extends Component{
    
constructor(props){
  super(props)
  this.state ={
    cardBlogs:[]
  }
}

componentDidMount(){
  axios.get('http://newsapi.org/v2/top-headlines?sources=techcrunch&apiKey=c39f8db983564f208079b2430925a028')
  .then(response =>{
    console.log('response cuy', response.data.articles)
    this.setState({cardBlogs:response.data.articles})
  })
  .catch(error =>{
    console.log('error cuy', error)
  })
}


  render(){
  return (
    <div className="container">
    <br/>
    <br/>
    <br/>
        <h1 style={{textAlign:'center'}}>Blog</h1>
        <br/>
        <div className="row d-flex justify-content-center" style={{marginBottom:50}}>
        {this.state.cardBlogs.map((cardBlog)=>(
                <div className="col-md-4">
                <div className="card text-center">
                    <img src={cardBlog.urlToImage} className="card-img-top" alt="" style={{width:'350px', height:'200px'}}/>
                    <div className="card-body">
                        <h5 className="card-title">{cardBlog.title}</h5>
                        <p>{cardBlog.description}</p>
                        <a href={cardBlog.url} target="blank" className="btn btn-primary">Selengkapnya</a>
                    </div>
                </div>
                <br/>
                </div>
        )
            )
        }
        </div>
    </div>
);
}
}
export default Blog