import React from 'react'
import {BrowserRouter as Router, Route, Link} from "react-router-dom";
import Registration from './Registration'

const styles ={
    container:{
        position:'relative',
        textAlign:'center',
    },
    imageCover:{
        width:'100%',
        grayscale:'100%'
    },
    blockText:{
        width:'100%',
        position:'absolute',
        top:'50%',
        left:'50%',
        transform:'translate(-50%, 50%)',
        color:'white',
        background:'grey',
        opacity:'90%'
    }
}

const Home = () => (

    <div style={styles.container}>
        <img 
            style={styles.imageCover}
            src="http://sketchrideproject.com/wp-content/uploads/2019/12/6.jpg?v=1576059830"
            alt="Sketching untuk pemula"
        />
        
        <div style={styles.blockText}>
            <h1>Sketching untuk pemula kuy !</h1>
            <h4>
                Jutaan orang sudah mencobanya dan berhasil dalam waktu singkat, ayo ikuti kelas seru dari pengajar yang profesional
                </h4>
            <a className="btn btn-primary" href="/registration">Daftar Sekarang Kuy !</a>
            <h1></h1>
        </div>
    </div>

)

export default Home